.banner {
  height: 100vh;
  width: 100vw;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #020203;
  display: flex;
  justify-content: space-between;
  padding: 0 4rem;
  transition: all 0.5s ease-in-out;

  .wrapper-left-center {
    display: flex;

    .banner__center {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-top: 100px;
      width: 440px;

      h1 {
        font-style: normal;
        font-weight: 700;
        font-size: 48px;
        line-height: 110.2%;
        margin: 16px 0;
      }

      p {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 27px;
        letter-spacing: -0.005em;
        margin-top: 0;
        margin-bottom: 32px;
      }
    }
  }

  .banner__right {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 240px;
    text-align: right;
    margin-top: 60px;

    .banner__right__trailer {
      width: 100%;
      height: 130px;
      position: relative;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      .play-button{
        position: absolute;
        width: 25%;
        height: 25%;
        bottom: 25%;
        left: 38%;
        cursor: pointer;
      }
    }
  }
}

.lateralBar {
  display: flex;
  flex-direction: column;
  margin-top: 150px;
  margin-right: 8.9rem;

  img {
    margin-bottom: 20px;
    cursor: pointer;
  }

  .blackwhite {
    filter: grayscale(100%);
  }
}

@media screen and (max-width: 996px) {
  .banner {
    justify-content: center;

    .wrapper-left-center {
      flex-direction: column-reverse;
      max-width: 400px;

      .banner__center {
        width: 300px;

        h1 {
          font-size: 36px;
        }
      }
    }

    .banner__right {
      width: 200px;
    }

    .lateralBar {
      margin-top: 60px;
      margin-right: 40px;
      flex-direction: row;

      img {
        margin-right: 20px;
      }
    }
  }
}

@media screen and (max-width: 610px) {
  .banner {
    padding: 0.5rem;

    .wrapper-left-center {
      width: 100%;
      margin-bottom: 20px;
    }

    .banner__right {
      height: 0;
      width: 0;
      display: none;
      visibility: hidden;
    }
  }

  .lateralBar {
    margin-right: 0;

    img {
      margin: 0 3px !important;
    }
  }
}
